import React from "react";

/**
 * item props
 */
export interface HeaderItemProps {
    icon: React.Component | React.FC | any;
    name: string;
    onClick?: () => void;
    order?: number;
}

/**
 * itemitem
 */
export abstract class AbstractHeaderItem {
    /**
     * item
     */
    abstract getHeaderItemInfo(): HeaderItemProps;
}